
.videolist-wrap{
  display: inline-block;
  width: 95%;
}
.videolist-text{
  text-align: left;
}
.videolist-text h2{
  width: 95%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 1rem;
  font-weight: bold;
  color: #333;
}
.videolist-text p{
  width: 95%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 0.8rem;
  font-weight: normal;
  color: #999;
}
.videolist-btn{
    width:100%;
    height:8rem;
    border:1px solid #e5e5e5;
    background: #EFEFEF;
    border-radius: 0.4rem;
    overflow: hidden;
    position: relative;
}
.playicon{
  position: absolute;
  width: 4rem;
  height: 4rem;
  left: 50%;
  top: 50%;
  margin-left: -2rem;
  margin-top: -2.5rem;
  z-index: 9;
}
.playicon img{
  width: 100%;
  height: 100%;
}
.videolist-btn img{
  width: 100%;
  height: 100%;
}
.done-img{
  position: absolute;
  right: 0;
  top: -0.5rem;
  width: 4.5rem;
  height: 4.5rem;
  z-index: 99;
}
.done-img img{
  width: 100%;
  height: 100%;
}
.videolist-btn i{
  position: absolute;
  right: 0;
  top: -0.3rem;
  color: #fff;
  font-style: normal;
  font-size: 0.8rem;
}
.videolist-btn p{
    width:100%;
    padding: 0 0.5rem;
    height:1.6rem;
    line-height: 1.6rem;
    color: #fff;
    font-size: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 0.4rem;
    background: rgba(0,0,0,0.5);
    text-align: left;
}
.processcontainer{
    width:100%;
    height:0.4rem;
    /*border:1px solid #e5e5e5;*/
    background: #e5e5e5;
    overflow: hidden;
    position: relative;
    position: absolute;
    left: 0;
    bottom: 0;
}
#processbar{
    background:#4CAF50;
    float:left;
    height:100%;
    text-align:center;
    line-height:150%;
}
.video-list-title{
    padding: 0.6rem 1rem;
    border-bottom: 1px solid #e5e5e5;
}
.video-list-title h2{
    font-size: 1rem;
    color: #333;
    font-weight: bold;
    /*margin-bottom: 0.8rem;*/
}
.video-list-title h2 span{
    margin-left: 0.4rem;
    font-weight: bold;
}
.video-list-title p{
    font-size: 1rem;
    color: #999;
    margin-top: 0.6rem;
    float: left;
    margin-right: 1rem;
}
.videolist{
    padding: 0 1rem;
}
.videolist li{
    display: inline-block;
    margin: 0.6rem 0 0 0;
    width: 50%;
    text-align: center;
}
.videolist-con{
  padding: 1rem 1rem;
  border-bottom: 1px solid #e5e5e5;
  position: relative;
}
.videolist-con span{
  font-size: 1rem;
  color: #333;
  margin-right: 1rem;
  font-weight: bold;
  display: inline-block;
  width:45%;
}
.videolist-con span:first-child{
  width: 18%;
}
.videolist-con i{
  position: absolute;
  right: 1rem;
  top: 0.6rem;
  font-size: 1rem;
  color: #fff;
  font-style: normal;
  padding: 0.3rem 1.5rem;
  background: #FF5722;
  border-radius: 0.3rem;
}



