
.vedio-header-wrap-group{
    position: fixed;
    top:0;
    left: 0;
    right: 0;
    z-index: 99;
}
.vedio-header-wrap{
  position: relative;
  height: 3rem;
  line-height: 3rem;
  background: rgba(0,0,0,0);
  text-align: center;
}
.vedio-header-wrap i{
  position: absolute;
  left: 1rem;
  top: 0rem;
  font-size: 2rem;
  color: #fff;
}
.vedio-header-wrap h2{
  text-align: center;
  font-size: 1.2rem;
  color: #fff;
  font-weight: normal;
}
.subtitle{
  padding: 0.6rem 1rem;
  border-bottom: 1px solid #e5e5e5;
  font-size: 0;
  position: relative;
}
.subtitle-text{
  width: 80%;
  display: inline-block;
}
.fanhui{
  width: 20%;
  display: inline-block;
  text-align: right;
 position: absolute;
 right: 1rem;
 top: 1.5rem;
}
.fanhui span{
  padding: 0.6rem 1rem;
  color: #fff;
  font-size: 1rem;
  background: #FF5722;
  border-radius: 0.3rem;
  vertical-align: middle;
}
.subtitle h2{
  font-style: normal;
  font-weight: bold;
  font-size: 1.2rem;
  color: #333;
  margin-bottom: 0.4rem;
}
.subtitle h2 span{
  color: #FF5722;
}
.subtitle p{
  font-size: 1rem;
  color: #999;
  margin-top: 0.2rem;
}
.myvideo{
  padding: 0rem;
  position: fixed;
  top: 0rem;
  left: 0;
  right: 0;
  z-index: 999;
}
.videonumber{
  padding: 1rem;
}
.videonumber p{
  display: inline-block;
  text-align: center;
  width: 20%;
  margin-bottom: 1rem;
  position: relative;
}
.videonumber p span{
  display: inline-block;
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 3.5rem;
  border: 1px solid #e5e5e5;
  line-height: 3.5rem;
  color: #333;
  font-size: 0.8rem;
  text-align: center;
}
.videonumber p i{
  display: inline-block;
  font-style: normal;
  font-size: 0.8rem;
  color: #fff;
  background:#FF5722;
  border-radius: 2rem;
  padding: 0rem 0.3rem;
  position: absolute;
  top: -0.5rem;
  left: 0.4rem;
  border: 3px solid #fff;
}
.color-change{
  border:1px solid #FF5722!important;
  color: #FF5722!important;
  font-weight: bold!important;
}



