
.mineheadbar-wrap {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
.mineheadbar {
  position: relative;
  height: 3rem;
  width: 100%;
  background: #FF5722;
  border-bottom: 1px solid #FF5722;
}
.mineheadbar i {
  color: #fff;
  position: absolute;
  font-size: 2rem;
}
.mineheadbar i.icon-fanhui {
  left: 1rem;
  top: 0rem;
}
.mineheadbar i.icon-gengduo {
  right: 1rem;
  top: 0rem;
}
.mine-top {
  padding: 1rem;
  background: #FF5722;
  text-align: center;
}
.mine-top p {
  color: #fff;
  font-size: 1rem;
  margin-top: 0.5rem;
}
.mine-top-img {
  display: inline-block;
  width: 5rem;
  height: 5rem;
  border: 1px solid #fff;
  border-radius: 5rem;
  overflow: hidden;
}
.mine-top-img img {
  width: 100%;
  height: 100%;
}
.mine-tab {
  font-size: 0;
  margin-bottom: 1rem;
}
.mine-tab a span {
  display: inline-block;
  height: 3rem;
  width: 50%;
  font-size: 1.2rem;
  font-weight: bold;
  background: #03A9F4;
  border-bottom: 2px solid #03A9F4;
  color: #fff;
  text-align: center;
  line-height: 3rem;
}
.color-white {
  border-bottom: 2px solid #fff !important;
}
.myhome-list {
  padding: 0.6rem 1rem;
  border-bottom: 1px solid #e5e5e5;
}
.myhome-list span {
  display: inline-block;
  font-size: 1.2rem;
  color: #999;
  width: 50%;
}
.myhome-list span i {
  font-style: normal;
  margin-right: 1rem;
}
.myhome-list span i:first-child {
  font-size: 1.6rem;
}
.myhome-list span:last-child {
  text-align: right;
}
.myhome-top {
  font-size: 0;
  padding: 0 1rem;
}
.myhome-top span {
  display: inline-block;
  text-align: left;
  width: 33.33%;
  padding: 1rem 0rem;
}
.myhome-top span p {
  font-size: 1rem;
  color: #999;
}
.myhome-top span h2 {
  font-size: 1.4rem;
  color: #333;
  font-weight: normal;
}
.myhome-icon {
  padding: 0.6rem 1rem;
  border-bottom: 1px solid #e5e5e5;
  position: relative;
}
.myhome-icon span {
  color: #999;
  font-size: 1rem;
  margin-right: 1rem;
}
.myhome-icon span i {
  margin-right: 1rem;
  /*font-size: 1.4rem;*/
}
.myhome-icon b {
  position: absolute;
  right: 1rem;
  top: 0;
  background: #03A9F4;
  padding: 0.4rem 2rem;
  border-radius: 0.2rem;
  color: #fff;
}



