
.mynav{
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 0rem 0;
  border-top: 1px solid #e5e5e5 ;
  font-size: 0;
  background: #f5f5f5;
  z-index: 999;
}
.nav-bottom{
  display: inline-block;
  width: 25%;
  text-align: center;
  position: relative;
}
.nav-bottom i{
  font-size: 2rem;
  color: #999;
}
.nav-bottom p{
  color: #333;
  font-size: 0.8rem;
  margin-top: -0.6rem;
}
.nav-bottom span{
  position: absolute;
  top:0.4rem;
  right: 18%;
  background: #FF5722;
  border-radius: 1rem;
  padding: 0rem 0.45rem;
  border: 1px solid #fff;
  color: #fff;
  font-size: 0.8rem;
  text-align: center;
}
.color-red1{
  color: #FF5722!important;
}



