
.jujiao{
		padding: 10rem 1rem 0;
		text-align: center;
}
.jujiao i{
		font-size: 8rem;
		color: #9E9E9E;
		font-style: normal;
}
.jujiao p{
		color: #999;
		font-size: 1rem;
		margin-top: -1.6rem;
}
.picload-input-wrap {
    padding: 0.8rem 1rem;
    border-top: 1px solid #e5e5e5;
    text-align: center;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
}
.a-upload {
    margin-top: 0.2rem;
    margin-right: 0.8rem;
    margin-left: 0.8rem;
    text-align: center;
    /*padding: 1.4rem 1.4rem;*/
    height: 4rem;
    width: 4rem;
    line-height: 4rem;
    position: relative;
    cursor: pointer;
    color: #888;
    background: #F44336;
    /*border: 1px solid #F44336;*/
    border-radius: 4rem;
    overflow: hidden;
    display: inline-block;
    *display: inline;
    *zoom: 1
}
.a-upload input {
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    width: 3rem;
    height: 3rem;
    filter: alpha(opacity=0);
    cursor: pointer;
    background: #fff;
    border: none;
}
.a-upload i {
    color: #999;
    font-size: 2rem;
    position: absolute;
    color: #fff;
    top: 0rem;
    left: 1rem;
}





