
.myalert-bg{
  position: fixed;
  top:0;
  left: 0;
  right: 0;
  height: 100%;
  background: rgba(0,0,0,0.6);
  z-index: 999;
  padding: 0 1rem;
}
.myalert{
  background: #fff;
  border-radius: 0.4rem;
  padding: 2rem;
  margin-top: 12rem;
}
.myalert p{
  text-align: center;
  color: #333;
  font-size: 1.2rem;
}
.myalert-btn{
  font-size: 0;
  margin-top: 1.6rem;
}
.myalert-btn span{
  display: inline-block;
  width: 100%;
}
.myalert-btn a{
  color: #fff;
  font-size: 1rem;
  padding: 0.3rem 0;
  display: inline-block;
  width: 100%;
  text-align: center;
  border-radius: 0.4rem;
  background: #FF5722;
}
