@charset "utf-8";
/* CSS Document */
html { font-size:62.5%; }
body { color:#333; font-size:1.6rem; font-weight:400; line-height:1.6; font-family:"Segoe UI","Lucida Grande",Helvetica,Arial,"Microsoft YaHei",sans-serif; }
body,dl,dd,ul,ol,h1,h2,h3,h4,h5,h6,p,form,input,textarea,select { margin:0; padding:0; }
a{outline:none; -webkit-tap-highlight-color:rgba(255,0,0,0);}
li { list-style:none;}
img{ border:none;}
html{-webkit-text-size-adjust:100%;}
html, body { position: relative; height: 100%; }
body,p{padding:0;margin:0;font-family: "tahoma","Arial","Hiragino Sans GB","Microsoft YaHei","\9ED1\4F53",Arial,sans-serif; }
a{ text-decoration:none;}
ul,li{margin:0;padding:0;list-style-type:none;}
.cl{ clear:both;}


body, nav, dl, dt, dd, p, h1, h2, h3, h4, ul, ol, li, input, button, textarea, footer {
    margin: 0;
    padding: 0;
}
*:not(input,textarea) {
    -webkit-touch-callout: inherit;
    -webkit-user-select: auto;
}
html{
    width: 100%;
    height: 100%;
}

body {
    font-family: "微软雅黑", Arial, sans-serif ,Microsoft Yahei,Simsun;
    font-size: 14px;
    color: #333;
    /*background-color: red;*/
    width: 100%;
    height: 100%;
    position: relative;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-text-size-adjust: none;
    /*min-width: 320px;*/
    /*max-width: 640px;*/
    /*overflow: hidden;*/
    margin: 0 auto;
}

h1, h2, h3, h4, h5, h6 {
    font-size: 100%;
}

form {
    display: inline;
}

ul, ol {
    list-style: none;
}

a {
    text-decoration: none;
    color: #1a1a1a;
}

a:hover, a:active, a:focus {
    /*color: #1c5aa2;*/
    /*color: #fff;*/
    text-decoration: none;
}

a:active {
    /*color: #aaa;*/
}
a, img {
    -webkit-touch-callout: none; /* 禁止长按链接与图片弹出菜单 */
}
img {
    vertical-align: middle;
    border: 0;
    -ms-interpolation-mode: bicubic;/*图片的缩放在IE7以及IE6下是件很恐怖的事情，将严重影响您的图片质量，欲解决，记得在img标签上加上此段代码：*/
}

button, input, select, textarea {
    font-size: 100%;
    vertical-align: middle;
    outline: none;
}

textarea {
    resize: none;
}

button, input[type="button"], input[type="reset"], input[type="submit"] {
    cursor: pointer;
    -webkit-appearance: button;
    -moz-appearance: button;
}

input:focus:-moz-placeholder, input:focus::-webkit-input-placeholder {
    color: transparent;
}

button::-moz-focus-inner, input::-moz-focus-inner {
    padding: 0;
    border: 0;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

/*清浮动*/
.clear { zoom:1; }/*ie浏览器清浮动*/
.clear:after { content:''; display:block; clear:both; }/*标准浏览器清浮动*/

/*rem换算适配代码*/
html {
  font-size: 82.5%;
}

@media only screen and (min-width: 481px) {
  html {
    font-size: 94% !important;
  }
}

@media only screen and (min-width: 561px) {
  html {
    font-size: 109% !important;
  }

}

@media only screen and (min-width: 641px) {
  html {
    font-size: 125% !important;
  }
}



