
.header-wrap-group{
    position: fixed;
    top:0;
    left: 0;
    right: 0;
    z-index: 99;
}
.header-wrap{
  position: relative;
  height: 3rem;
  line-height: 3rem;
  background: #FF5722;
  text-align: center;
}
.header-wrap i{
  position: absolute;
  left: 1rem;
  top: 0rem;
  font-size: 2rem;
  color: #fff;
}
.header-wrap h2{
  text-align: center;
  font-size: 1.2rem;
  color: #fff;
  font-weight: normal;
}



